import Axios from 'axios'
import { httpService } from './http.service.js'

const axios = Axios.create({
    withCredentials: true
})

const STORAGE_KEY = 'loggedinUser'
const ENDPOINT = 'auth/'

export const userService = {
    login,
    logout,
    signup,
    saveUserToStorage,
    clearUserFromStorage,
    getUserFromStorage
}

window.us = userService

async function login(credentials) {
    // console.log('login - credentials', {data: credentials})
    try {
        const res = await httpService.post(ENDPOINT + 'login', credentials)
        return res
    } catch (error) {
        console.log(error)
        throw error
    }
}

async function signup(userInfo) {
    const user = { ...userInfo }
    const res = await httpService.post(ENDPOINT, user)
    sessionStorage.setItem(STORAGE_KEY, JSON.stringify(user))
    return user
}

async function logout() {
    try {
        const res = await httpService.post(ENDPOINT + 'logout')
        sessionStorage.removeItem(STORAGE_KEY)
        return res
    } catch (error) {
        console.log('logout - error', error)
    }
}

function saveUserToStorage(user) {
    sessionStorage.setItem(STORAGE_KEY, JSON.stringify(user))
}

function clearUserFromStorage() {
    sessionStorage.clear(STORAGE_KEY)
}

function getUserFromStorage() {
    return JSON.parse(sessionStorage.getItem(STORAGE_KEY))
}

