import React, { useEffect, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { Link, useParams } from "react-router-dom"
import { ReviewForm } from "../cmps/review-form"
import { getCloudUrl } from "../services/cloudinary-service"
import { toyService } from "../services/toy.service"
import { loadReviews, addReview, removeReview } from "../store/actions/review.actions"
export const ToyDetails = (props) => {
    // const initState = {
    //     toy: null,
    //     newReview: {
    //         title: '',
    //         description: ''
    //     }
    // }

    const dispatch = useDispatch()
    const params = useParams()
    const [toy, setToy] = useState(null)
    const { reviews, user } = useSelector((storeState) => {
        return {
            reviews: storeState.reviewModule.reviews,
            user: storeState.userModule.user
        }
    })

    useEffect(() => {
        loadToy()
        initReviews()
    }, [])

    const loadToy = async () => {
        const toy = await toyService.getById(params.toyId)
        setToy(toy)
    }

    const initReviews = async () => {
        dispatch(loadReviews({ id: params.toyId, type: 'byToy' }))

    }

    const onDelete = (id) => {
        dispatch(removeReview(id))
    }

    const onAddReview = (values) => {
        const { title, description } = values
        dispatch(addReview(({ title, description, toyId: toy._id, userId: user._id })))
        initReviews()
    }

    const isAdmin = user ? user.isAdmin : false
    if (!toy) return <h1>Loading...</h1>
    const { name, price, labels, inStock, _id, imgUrl } = toy
    const cloudUrl = getCloudUrl(imgUrl, 250)
    return <main className="toy-details">
        <section className="toy-info-container">
            <img src="" alt="" />
            <h2>Name: {name}</h2>
            <img src={cloudUrl} alt="" />
            <h2>Price: {price}</h2>
            <h2>Labels: {labels.join(', ')}</h2>
            <h2>In Stock: {inStock ? 'Yes' : 'No'}</h2>
        </section>
        {isAdmin &&
            <section className="details-tools">
                <Link className="btn" to={'/toy/edit/' + _id}>
                    Edit
                </Link>
            </section>
        }
        <hr />
        <h1>Reviews</h1>
        {user && <ReviewForm doSubmit={onAddReview} />}
        {reviews &&
            <section className="review-container">
                {reviews.map(review => {
                    console.log('ToyDetails - review', review)
                    return <section key={review._id} className="review-preview">
                        <h2>{review.title}</h2>
                        <h5>{review.byUser.username}</h5>
                        <p>{review.description}</p>
                        {isAdmin && <button onClick={ev => onDelete(review._id)}>X</button>}
                    </section>
                })}
            </section>
        }
    </main >
}