import React from "react"
import { connect } from "react-redux"
import { Link } from "react-router-dom"
import { ToyFilter } from "../cmps/toy-filter"
import { ToyList } from "../cmps/toy-list"
import { showSuccessMsg, showErrorMsg } from "../services/event-bus.service"
import { setFilter } from "../store/actions/filter.action"
import { loadToys, deleteToy } from "../store/actions/toy.action"
import { Pagination, Skeleton } from "@mui/material"

class _ToyApp extends React.Component {

    componentDidMount() {
        this.props.loadToys()
    }

    onSetFilter = (ev) => {
        console.dir(ev.target)
        const filterPackage = {
            checked: ev.target.checked,
            value: ev.target.value,
            field: ev.target.name,
        }
        this.props.setFilter(filterPackage)
        this.props.loadToys()
    }

    onDeleteToy = async (id) => {
        try {
            await this.props.deleteToy(id)
            showSuccessMsg('Toy deleted')
        } catch (err) {
            console.error(err)
            showErrorMsg('Cannot delete toy')
        }
    }

    render() {
        const { toys, filterBy } = this.props
        const pageCount = Math.floor(toys.length / 5)
        // const toys = null
        return <main className="toy-wrapper">
            <aside className="toy-side">
                <Link to='/toy/edit/' className="btn">Add</Link>
                <ToyFilter onSetFilter={this.onSetFilter} />
            </aside>
            < ToyList toys={toys} deleteToy={this.onDeleteToy} />
            {pageCount > 0 && <Pagination className="pagination-toys" count={pageCount} page={+filterBy.page} onChange={(ev, value) => {
                ev.target.value = value
                ev.target.name = 'page'
                this.onSetFilter(ev)
            }} />}
        </main>
    }
}

const mapStateToProps = (storeState) => {
    return {
        toys: storeState.toyModule.toys,
        filterBy: storeState.filterModule.filterBy,
    }
}

const mapDispatchToProps = {
    loadToys,
    deleteToy,
    setFilter
}

export const ToyApp = connect(
    mapStateToProps,
    mapDispatchToProps
)(_ToyApp)
