import React from 'react'
import { Formik } from 'formik'
import { showSuccessMsg, showErrorMsg } from '../services/event-bus.service'

export const ReviewForm = ({ doSubmit }) => {

    return <Formik
        initialValues={{ title: '', description: '' }}
        enableReinitialize={true}
        validateOnChange={false}
        validateOnBlur={false}

        onSubmit={async (values, actions) => {
            try {
                console.log('onSubmit - values', values)
                doSubmit(values)
                showSuccessMsg(`Review added`)
            } catch (error) {
                console.log('onSubmit - error', error)
                showErrorMsg('Something went wrong')
            } finally {
                actions.setSubmitting(false)
            }
        }}

        validate={values => {
            const errors = {};
            if (!values.title) {
                errors.title = 'Required'
                showErrorMsg('Please write a review first!')
            }
            if (!values.description) {
                errors.description = 'Required'
                showErrorMsg('Please write a review first!')
            }
            return errors;
        }}
    >
        {({ isSubmitting, handleSubmit, handleChange }) => {
            return <section className='review-form'>
                <section className='review-nesting title'>
                    <label htmlFor="title">Title</label>
                    <input type="text" id="title" name="title" onChange={handleChange} />
                </section>
                <section className='review-nesting description'>
                    <label htmlFor="description">Description</label>
                    <textarea name="description" id="description" style={({ resize: 'none' })} onChange={handleChange}></textarea>
                </section>
                <button type='submit' className='btn' disabled={isSubmitting} onClick={handleSubmit}>Submit</button>
            </section>
        }}

    </Formik>
}
