
const initialState = {
    toys: [],
    labels: ['adult', 'comic', 'battery powered', 'baby', 'puzzle', 'video games']
}

export function toyReducer(state = initialState, action) {
    var toys
    switch (action.type) {
        case 'SET_TOYS':
            console.log('{ ...state, toys: action.toys }',  action.toys)
            return { ...state, toys: action.toys }
        case 'ADD_TOY':
            toys = [action.toy, ...state.toys]
            return { toys, ...state }
        case 'REMOVE_TOY':
            toys = state.toys.filter((toy) => toy._id !== action.toyId)
            return { ...state, toys }
        case 'UPDATE_TOY':
            toys = state.toys.map(currToy =>
                (currToy._id === action.toy._id) ? action.toy : currToy)
            return { toys, ...state }
        default:
            return state
    }
}