import React from "react"
import { useSelector } from "react-redux"
import { Link, useNavigate } from "react-router-dom"
import { getCloudUrl } from "../services/cloudinary-service"

export function ToyPreview({ toy, deleteToy }) {

    const { name, price, labels, inStock, _id, imgUrl } = toy
    const user = useSelector((storeState) => storeState.userModule.user)
    const isAdmin = user ? user.isAdmin : null
    const cloudUrl = getCloudUrl(imgUrl)
    const navigate = useNavigate()

    return <section className="toy-preview">
        <section>
            <h3>{name}</h3>
            <img src={cloudUrl} alt="" onClick={ev => {
                navigate(`/toy/${_id}`)
            }} />
            <h3>{price}$</h3>
            <h3>{labels.join(', ')}</h3>
            <h3>{inStock ? 'In Stock' : 'Out of Stock'}</h3>
        </section>
        <section>
            <Link to={'/toy/' + _id} className="btn">
                Details
            </Link>
            {isAdmin &&
                <Link to={'/toy/edit/' + _id} className="btn">
                    Edit
                </Link>
            }
            {isAdmin && <button className="btn" onClick={(ev) => deleteToy(_id)}>X</button>}
        </section>
    </section>
}