import { useEffect, useState } from "react"
import { useDispatch, useSelector } from "react-redux"
import { NavLink } from "react-router-dom"
import { userService } from "../services/user.service"
import { getActionSetUser, loginUser, logoutUser } from "../store/actions/user.action"


export const AppHeader = (props) => {
    const user = useSelector((storeState) => storeState.userModule.user)
    const dispatch = useDispatch()
    useEffect(() => {
        const userFromStorage = userService.getUserFromStorage()
        if (!user && userFromStorage) {
            dispatch(getActionSetUser(userFromStorage))
        }

    }, [])
    return <header className="full">
        <section className="app-header">
            <h1>Toys B Me</h1>
            <nav>
                <NavLink to="/">Home</NavLink>
                <NavLink to="/toy">Toys</NavLink>
                <NavLink to="/about">About</NavLink>
                {/* <NavLink to="/dashboard">Dashboard</NavLink> */}
            </nav>
            {user && <button className="btn" onClick={ev => {
                userService.logout()
                dispatch(logoutUser())
            }}>Logout</button>}
        </section>
    </header>

}