import React, { useEffect, useState } from "react"
import { useDispatch } from "react-redux"
import { useNavigate, useParams } from "react-router-dom";
import { showSuccessMsg, showErrorMsg } from "../services/event-bus.service";
import { toyService } from "../services/toy.service"
import { deleteToy, saveToy } from "../store/actions/toy.action"
import { EditForm } from "./edit-form";

export const ToyEdit = (props) => {

    const [toy, setToy] = useState(toyService.getEmptyToy())
    const params = useParams()
    const dispatch = useDispatch()
    const navigation = useNavigate()

    useEffect(() => {
        if (!params.toyId) {
            console.log('new toy')
            return
        }
        ; (async () => {
            const res = await toyService.getById(params.toyId)
            setToy(res)
        })()
    }, [])

    const handleInput = (ev) => {
        const field = ev.target.name
        const { value } = ev.target
        if (field === 'inStock') return setToy((prevToy) => ({ ...prevToy, inStock: !prevToy.inStock }))
        setToy((prevToy) => ({ ...prevToy, [field]: value }))
    }

    const onSubmit = () => {
        try {
            dispatch(saveToy(toy))
        } catch (err) {
            console.log(err)
        }
        navigation('/toy')
    }

    const onDelete = (id) => {
        dispatch(deleteToy(id))
        navigation('/toy')
    }

    console.log('toy', toy);
    if (!toy) return <h1>Loading...</h1>
    return <EditForm toy={toy} onInput={handleInput} onSubmit={onSubmit} onDelete={onDelete} />

}
