import { showSuccessMsg, showErrorMsg } from "../../services/event-bus.service"
import { userService } from "../../services/user.service"

// get actions
export function getActionSetUser(user) {
    return { type: 'SET_USER', user }
}

export function getActionRemoveUser() {
    return { type: 'REMOVE_USER', user: null }
}

// update backend and dispatch
export function loginUser(credentials) {
    return async (dispatch) => {
        try {
            const res = await userService.login(credentials)
            dispatch(getActionSetUser(res))
            userService.saveUserToStorage(res)
            return res
        } catch (err) {
            console.log(err)
        }
    }
}

export function logoutUser() {
    return async dispatch => {
        try {
            const res = await userService.logout()
            dispatch(getActionSetUser())
            showSuccessMsg('Logged out')
        } catch (err) {
            showErrorMsg('Something went wrong')
            throw err
        }
    }

}