import React from "react"
import { Formik, Form } from 'formik'
import { useDispatch } from "react-redux"
import { Button } from "@mui/material"

import { showSuccessMsg, showErrorMsg } from "../services/event-bus.service"
import { loginUser } from "../store/actions/user.action"

export function UserLogin({ user }) {
    const dispatch = useDispatch()
    return <section className="login-container">
        <Formik
            initialValues={{ username: '', password: '' }}
            enableReinitialize={true}
            validateOnChange={false}
            validateOnBlur={false}
            onSubmit={async (values, actions) => {
                try {
                    const res = await dispatch(loginUser(values))
                    showSuccessMsg(`Welcome ${res.username}`)
                } catch (error) {
                    console.log('onSubmit - error', error)
                    showErrorMsg('Something went wrong')
                }
                actions.setSubmitting(false)
            }}
            validate={values => {
                const errors = {};
                if (!values.username) {
                    errors.username = 'Required';
                }
                if (!values.password) {
                    errors.password = 'Required'
                }
                return errors;
            }}
        >
            {({ isSubmitting, errors, handleSubmit, handleChange }) => (
                <Form onSubmit={handleSubmit}>
                    <input type="text" name="username" placeholder="Username" autoComplete="off" onChange={handleChange} />
                    <input type="password" name="password" placeholder="Password" autoComplete="off" onChange={handleChange} />
                    <Button variant="outlined" sx={{ hight: 1 }} type="submit" disabled={isSubmitting}>Login</Button>
                </Form>
            )
            }
        </Formik >
    </section>
}